.accordion {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.accordion__item {
  display: flex;
  flex-direction: column;
}
.accordion__item.active .accordion__btn-icon {
  background-color: var(--bg-f);
  transform: rotate(45deg);
}
.accordion .editor {
  padding: 16px clamp(10px, 5.556vw, 80px) 0 clamp(70px, 11.343vw, 87px);
}
.accordion__btn {
  width: 100%;
  padding: clamp(10px, 1.695vw, 13px) clamp(10px, 3.911vw, 30px);
  border: 1px solid var(--br-a);
  border-radius: 7px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: clamp(16px, 3.129vw, 24px);
  line-height: 120%;
  font-weight: 700;
  text-align: left;
  transition: all 0.3s;
}
.accordion__btn:hover .accordion__btn-icon {
  border: 1px solid var(--br-B);
}
.accordion__btn-icon {
  position: relative;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border: 1px solid transparent;
  border-radius: 100px;
  flex-shrink: 0;
  transition: all 0.4s;
}
.accordion__btn-icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 14px;
  height: 2px;
  background: var(--cl-c);
  transition: all 0.3s;
}
.accordion__btn-icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 14px;
  width: 2px;
  background: var(--cl-c);
  transition: all 0.3s;
}

