@media (max-width: 1024px) {
  .header .burger {
    --burger-width: 26px;
    --burger-height: 32px;
    --burger-line-height: 1px;
    --border-radius: 0;
    position: relative;
    z-index: 100;
    border: none;
    width: var(--burger-width);
    height: var(--burger-height);
    flex-shrink: 0;
    cursor: pointer;
  }
  .header .burger::before, .header .burger::after {
    content: "";
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    height: var(--burger-line-height);
    background: var(--bg-a);
    transform-origin: left top;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out, opacity 0.5s, background 0.3s ease-in-out;
    border-radius: var(--border-radius);
  }
  .header .burger::before {
    top: 10px;
  }
  .header .burger::after {
    bottom: 10px;
  }
  .header .burger__line {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100%;
    height: var(--burger-line-height);
    background: var(--bg-a);
    transform: translate(-50%, -50%);
    transition: transform 0.3s ease-in-out, opacity 0.5s, background 0.3s ease-in-out;
    border-radius: var(--border-radius);
  }
  .header .burger:hover {
    opacity: 0.7;
  }
  .header .burger.active::before {
    background: var(--bg-a);
    top: 50%;
    transform: rotate(45deg) translate(-50%, -50%);
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }
  .header .burger.active::after {
    background: var(--bg-a);
    top: 50%;
    transform: rotate(-45deg) translate(-50%, -50%);
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }
  .header .burger.active .burger__line {
    background: var(--bg-a);
    transform: scale(0);
    transition: transform 0.3s ease-in-out;
  }
}

